 

body h2, p{
  margin:0;
  padding:0;


}
  
  

body{
  padding:3em;
  background: fixed;
  background-image: url(./images/bg-desktop.svg); background-color: rgb(67, 35, 128);
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400px;
  color:rgb(244, 242, 247);;
}


header img{
  width: 10%;
}
main {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;


}

main img{
  width: 40%;  
  
}

.content {
  width:45%;
  padding-top:2em;
  margin-left: 10%;
  
  
}
.content h2{
  color:rgb(244, 242, 247);
  font-weight: 600px;
  font-family: 'Poppins', sans-serif;

}
.content p{
  margin:2em 0em;
  /* font-size:15px; */
  font-family: 'Poppins', sans-serif;
  font-weight: 400px;
  color:rgb(244, 242, 247);
  
}
main button{
  padding:1em;
  border:none;
  border-radius: 3em;
  color: rgb(67, 35, 128);
  width: 50%;

}

footer{
 
  display: flex;
  justify-content:space-between;
   
  
 
}

  

